:root {
  --color-primary: #1abc9c;
  --color-primary-dark-blue: #474e5d;
  --color-primary-8: rgba(26, 188, 156, 0.8);
  --color-primary-dark-blue-8: rgba(71, 78, 93, 0.8);
  --color-primary-white: #ffffff;
  --color-primary-ligh-grey: #f5f6f7

  --color-white: #fff;
  --color-grey: #555;
}
html {
  font-size: 62.5%; /* 1rem = 10px, 10px/16px = 62.5% */
}
body {
  font: 2rem Montserrat, sans-serif;
  line-height: 1.8;
  color: var(--color-primary-ligh-grey);
}
p {font-size: 1.6rem;}

.margin {margin-bottom: 4.5rem;}

.navbar {
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
  border: 0;
  border-radius: 0;
  margin-bottom: 0;
  font-size: 1.2rem;
  letter-spacing: 0.5rem;
}

.navbar-nav li a:hover {
  color: var(--color-primary) !important;
}

.bg-1 {
  background-image:  linear-gradient(to right bottom, rgba(26, 188, 156, 0.8),rgba(71, 78, 93, 0.8)),url(../img/money.jpg);
  /* background-color: var(--color-primary); */
  color: var(--color-white);
}
.bg-2 {
  background-color: var(--color-primary-dark-blue);
  color: var(--color-white);
}
.bg-3 {
  background-color: var(--color-primary-white);
  color: var(--color-grey);
}
.bg-4 {
  background-color: #2f2f2f;
  color: var(--color-white);
}
.container-fluid {
  padding-top: 3rem;
  padding-bottom: 3rem;
}

div#Currencytab {
  background: #fff;
  margin: 1rem auto;
  padding: 5rem;
  border-radius: .5rem;
}
.nav-tabs>li {
  height: 5.2rem;
  line-height: 5.2rem;
}
.nav-tabs>li.active>a, .nav-tabs>li.active>a:focus, .nav-tabs>li.active>a:hover {
    color: #555;
    cursor: default;
    background-color: #fff;
    border: 1rem solid #ddd0;
    /* border: transparent; */
}

@-webkit-keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}
@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}
#notification{
  visibility: hidden;
  min-width: 25.0rem;
  margin-left: -12.5rem;
  background-color: #323232;
  color: #fff;
  text-align: center;
  border-radius: .2rem;
  padding: 1.6rem;
  position: fixed;
  z-index: 1;
  left: 50%;
  bottom: 3.0rem;
}
@media (min-width: 640px) {
  #notification{
    min-width: 28.8rem;
    max-width: 56.8rem;
    border-radius: .2rem;
  }
}

#notification.show{
  visibility: visible;
  -webkit-animation: fadein 0.5s;
  animation: fadein 0.5s;
}
